body{
  background-color:#87CEFA;
}
.App{
  background-color: #D3D3D3;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#formheader{
  background-color: black;
}
#form input{
  background-color: rgba(27, 112, 1);
  border: 0; 
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#form input:focus{
  outline: none;
}
#form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgba(202, 156, 24, 1); 
  cursor: pointer;
  position: relative;
}
#form button:focus{
  outline: none;
}